"use strict";
var libs = process.cwd() + '/libs/';
var Article = require(libs + 'model/article');
const articlesService = require(libs + 'service/articlesService');
const url = require('url');
let contentResponses;

class articlesController {

    static async articleMethod(req, res) {
        console.log('I am here');
        const countOfItems = url.parse(req.url, true).query.counts;
        const tmp = (typeof(countOfItems)).localeCompare("undefined");
        try {
            if(tmp == 0){
                contentResponses = await articlesService.getAllContents(req, res, 1000);
            }else {
                contentResponses = await articlesService.getAllContents(req, res, countOfItems);
            }

            console.log(contentResponses);

            res.send(contentResponses);

            // res.send(contentResponses);
        } catch (error) {
            //console.log("Error: agregateController/getAllContents");
        }
    }
}

module.exports = articlesController;